@each $key, $val in $colors {
	.text-#{$key} {
		color: $val;
	}

	.text-hover-#{$key} {
		&:hover {
			color: $val;
		}
	}

	.bg-#{$key} {
		background: $val;
	}

	@if ($val != black and $val != white) {
		// light variations
		@for $i from 1 through 9 {
			.text-#{$key}-light-#{$i} {
				color: mix(white, $val, $i * 6.625%);
			}

			.text-hover-#{$key}-light-#{$i} {
				&:hover {
					color: mix(white, $val, $i * 6.625%);
				}
			}

			.bg-#{$key}-light-#{$i} {
				background: mix(white, $val, $i * 6.625%);
			}
		}

		// dark variations
		@for $i from 1 through 9 {
			.text-#{$key}-dark-#{$i} {
				color: mix(black, $val, $i * 6.625%);
			}

			.text-hover-#{$key}-dark-#{$i} {
				&:hover {
					color: mix(black, $val, $i * 6.625%);
				}
			}

			.bg-#{$key}-dark-#{$i} {
				background: mix(black, $val, $i * 6.625%);
			}
		}
	}
}
